@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Source Sans Pro', sans-serif;
}
#root{
  height: 100%;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
p{
  font-size: 15px !important;
}
th{
  background-color: grey;
}

.navbar{
  background-color: black !important;
}
.nav-link{
  color: whitesmoke !important;
}
.navbar-brand{
  color: white !important;
}
.navbar-nav{
  margin-left: auto !important;
}

.smallimg{
  height: 200px;
  width: 100% !important;
  border-radius: 5px !important;
}
h1{
  font-size: 20 px !important;
}
.btn{
  background-color: black !important;
  color: white !important;
  box-shadow: none !important;
}
.bs{
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  margin-top: 20 px !important;
  border-radius: 5px !important;
  padding: 20px !important;
}
input, select, .ant-picker{
  box-shadow: none !important;
  margin-top: 10px;
  border: 1px solid black !important;
  width: 100% !important;
  border-radius: 3px !important;
  height: 35px !important;
}
h2{
  font-size: 30px ;
  padding: 10px;
  text-align: center;
  font-weight: bold ;
}
.bigimg{
  height: 400px;
  border-radius: 5px !important;
}
.dropdown-toggle{
  border: 0px solid black !important;
}
.c{
  background-color: whitesmoke !important;
  color: gray;
  box-shadow: none !important;
  border-color: black;
}
.bgimg {
  background-image: url("https://cdn-image.travelandleisure.com/sites/default/files/styles/1600x1000/public/hotel-interior-room0416.jpg?itok=5gENxAK1");
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.container {
  background-color: rgba(255, 255, 255, 0);
}

.regimg {
  background-image: url("https://www.forniturealberghiereonline.it/images/minimal/design-hotel-stile-minimal.jpg") !important;
  background-size: cover;
  background-repeat: no-repeat;
  height: 100%;
}

.bs {
  background-color: white;
}

.loginimg {
  background-image: url("https://cache.marriott.com/marriottassets/marriott/DELAL/delal-king-room-5932-hor-clsc.jpg?interpolation=progressive-bilinear&");
  background-size: cover;
  background-repeat: no-repeat;
  height: 100%;
}

.ant-picker-input input{
  border: none !important;
}
.fcc-btn {
  background-color: #4da2f7;
  color: white;
  padding: 15px 25px;
  position: fixed;
  text-decoration: none;
  left:0px;
}
.landing{
  background-color: black!important;
  height: 100vh;
  padding-top: 130px;
}
.landingbtn{
  background-color: white !important;
  color: black !important;
  margin-top: 20px;
}


.qwe img {
  display: block;
  margin: 0 auto;
  width: 50px;
}
.App {
  height: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

